@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{
  text-align: center;
}

h1.pageTitle{
  font-family: Nunito;
  font-size: 50px;
  color: #ff7575;
}


.infoContainer{
  width: 75%;
  margin: 0px auto;
}

.movie-container{
    width: 36%;
    float: left;
    display: inline-block;
    background: #FFA8A8;
    background: linear-gradient(to right, #FFA8A8, #ffd8d8);
    border-radius: 3%;
    padding-top: 20px;
    padding-bottom: 36px;
}

.search-results{
	margin: 0px auto;
	width:60%;
	font-family: Nunito;
    font-size: 40px;
}

.movie-container ul {
	list-style: none;
	padding: 0px;
	width:60%;
	margin: 0px auto;
}

.movie-container ul li{
	cursor: pointer;
	padding-bottom: 5px;
	padding-right: 50px;
	font-family: Nunito;
	font-weight: bolder;
}

.movie-container ul li:hover{
	color: #FF7575;
	text-decoration: underline;
}
*
{
    outline: none;
}

html, body
{
    height: 100%;
    min-height: 100%;
}

body
{
    margin: 0;
    background-color: #ffd8d8;
}

.tb
{
    display: table;
    width: 100%;
}

.td
{
    display: table-cell;
    vertical-align: middle;
}

input, button
{
    color: #fff;
    font-family: Nunito;
    padding: 0;
    margin: 0;
    border: 0;
    background-color: transparent;
}

.cover
{
    width: 550px;
    padding: 35px;
    margin: 0px auto;
    background-color: #ff7575;
    border-radius: 20px;
    box-shadow: 0 10px 40px #ff7c7c, 0 0 0 20px #ffffffeb;
    transform: scale(0.6);
}


input[type="text"]
{
    width: 100%;
    height: 96px;
    font-size: 60px;
    line-height: 1;
}

input[type="text"]::-webkit-input-placeholder
{
    color: #e16868;
}

input[type="text"]:-ms-input-placeholder
{
    color: #e16868;
}

input[type="text"]::-ms-input-placeholder
{
    color: #e16868;
}

input[type="text"]::placeholder
{
    color: #e16868;
}


button
{
    position: relative;
    display: block;
    width: 84px;
    height: 96px;
    cursor: pointer;
}

.s-circle
{
    position: relative;
    top: -8px;
    left: 0;
    width: 43px;
    height: 43px;
    margin-top: 0;
    border-width: 15px;
    border: 15px solid #fff;
    background-color: transparent;
    border-radius: 50%;
    transition: 0.5s ease all;
}

button span
{
    position: absolute;
    top: 68px;
    left: 43px;
    display: block;
    width: 45px;
    height: 15px;
    background-color: transparent;
    border-radius: 10px;
    transform: rotateZ(52deg);
    transition: 0.5s ease all;
}

button span:before, button span:after
{
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    width: 45px;
    height: 15px;
    background-color: #fff;
    border-radius: 10px;
    transform: rotateZ(0);
    transition: 0.5s ease all;
}

.s-cover:hover .s-circle
{
    top: -1px;
    width: 67px;
    height: 15px;
    border-width: 0;
    background-color: #fff;
    border-radius: 20px;
}

.s-cover:hover span
{
    top: 50%;
    left: 56px;
    width: 25px;
    margin-top: -9px;
    transform: rotateZ(0);
}

.s-cover:hover button span:before
{
    bottom: 11px;
    transform: rotateZ(52deg);
}

.s-cover:hover button span:after
{
    bottom: -11px;
    transform: rotateZ(-52deg);
}
.s-cover:hover button span:before, .s-cover:hover button span:after
{
    right: -6px;
    width: 40px;
    background-color: #fff;
}
.movie-card{
	width: 30%;
	display: inline-block;
}

.movieName{
	font-family: Nunito;
    font-size: 27px;
}


.poster{
	width:350px;
}

img::before {
    content   : "";
    position  : absolute;
    top       : 100%;
    width     : inherit;
    height    : inherit;
    opacity   : 0.45;

    background-image: inherit;
    transform: rotateX(180deg);
    -ms-box-shadow: inset 0 150px 50px -12px #fff;
     -o-box-shadow: inset 0 150px 50px -12px #fff;
        box-shadow: inset 0 150px 50px -12px #fff;
}
